import { useRouter } from "next/router";
import { securePage } from "../../../components/common/secure_page";
import { Account } from "../../../hooks/use_account";
import { useCssStateManager } from "../../../hooks/use_css_state_manager";
import { SmartState } from "../../../hooks/use_smart_state";
import AccountFirstLoginAddressBody from "../../../components/account/first-login/address/body";
import { useEffect } from "react";
import { misc } from "../../../lib/misc";

export type AccountYourAddressesEditProps = {
    result: {};
    account: SmartState<Account | null>;
};
  
const AccountYourAddressesEdit = securePage(function({result,account}: AccountYourAddressesEditProps) {
    const queryParams = misc.getQueryParams();
    const { id,checkout } = queryParams as {id: string,checkout?: string};

    return <AccountFirstLoginAddressBody context="manage" subContext="edit" editId={id} cancelPath="/account/your-addresses" {...(checkout?{checkoutUrl: checkout}:{})} />;
});

export default AccountYourAddressesEdit;